import styled from 'styled-components';

import Touch from './assets/Handsss.png';

//Home.js Styles
export const StyledHome = styled.div`
@import url('https://fonts.googleapis.com/css?family=KoHo:400,500&display=swap');
background: #080808;
height: 100%;

.empty{
  width: 100%;
  height: 100%;
  background: #080808;
}

.home-contents{
  display: flex;
  flex-direction: column;
  justify-content: center;
  background: #080808;
  color: #fff;
  height: 80%;
  position: relative;
  z-index: 1;

  .home-text-container{
    height: 260px;
    padding-left: 20px;
  }

  


  .hand-container{
    position: fixed;
    bottom: -41px;
    display: flex;
    width: 100%;
    z-index: -1;
    justify-content: center;
    .hand-sizer{
      height: 425px;
      width: 700px;
      display: flex;
      justify-content: center;

      .hands-img{
        height: 100%;
      }
    }
  }
  
  .welcome-container{
    
    display: flex;
    align-items: center;
    height: 60%;
    width: 1200px;
    margin: 0 auto;
    justify-content: space-between;
    @media(max-width: 1200px){
      width: 100%;
    }
    @media(max-width: 650px){
      flex-direction: column;
    }

    .welcome-text{
      font-family: 'KoHo', sans-serif;
      font-size: 3rem;
      color: #ffffff;
      font-weight: 700;
      margin-block-start: 0em;
      margin-block-end: 0em;
      @media(max-width: 750px){
        font-size: 2.5rem;
      }
    }
    .animated-text{
      font-family: 'KoHo', sans-serif;
      font-size: 2rem;
      color: #ffffff;
      margin-block-start: 0em;
      margin-block-end: 0em;
      @media(max-width: 750px){
        font-size: 1.3rem;
      }
    }
  }

  .logos{
    @media(max-width:650px){
      margin-left: 20px;
    }
    .logo{
      height: 48px;
      margin-right: 40px;
      margin-top: 20px;
    }
  }

  .modal-container{
    margin-right: 30px;
    display: flex;
    flex-direction: column;
    width: 110px;
    border-radius: 9px;
    box-shadow:  -10px 10px 0px #c6e8f2, 
             10px -10px 0px #336075;
    top: 50%;
    left: calc(50% - 130px);
    height: 180px;
    justify-content: space-evenly;
    @media(max-width: 650px){
      flex-direction: row;
      width: 250px;
      margin-right: 0px;
      height: 80px;
      background: #0b0b0b;
    }
  }

  .show-modal-button{
  font-family: 'KoHo', sans-serif;
  color: #ffffff;
  background: transparent;
  font-size: 1em;
  border: none;
  cursor: pointer;
  text-align: center;
  font-weight: 700;
  user-select: none;
  height: 70px;
  width: 100px;
  
  

  @media(max-width:499px){
   
  }
}
.show-modal-button:hover,
.show-modal-button:focus {
  border: none;
  font-size: 1.1em;
  outline: none;
}

  

  .modal {
    width: 300px;
    height: 300px;
    color: #ffffff;
    border-radius: 8px;
    padding: 20px;
    border-radius: 8px;
    background: rgba(0,0,0,.9);
    box-shadow: -10px 10px 0px #c6e8f2, 10px -10px 0px #336075;
    font-family: 'KoHo', sans-serif;
    position: fixed;
    z-index: 90;
   
    top: calc(50% - 110px);
    left: calc(50% - 170px);
    display: flex;
    flex-direction: column;

    .modal-title{
      margin-block-start: 0em;
      margin-block-end: 0em;
    }

    .modal-content{
      margin-block-start: .6em;
      list-style: none;
      text-align: justify;
      margin-block-end: .2em;

      li{
        list-style: none;
        margin-block-end: .2em;
      }

     

      a{
        text-decoration: underline;
        color: #c6e8f2;
      }
    }
  }
  
  .modal-close-button {
    padding: 6px;
    background-color: black;
    color: #ffffff;
    font-weight: 700;
    font-size: 1.2em;
    border: none;
    position: absolute;
    top: 10px;
    right: 15px;
    width: 30px;
    cursor: pointer;
    transition: background-color 0.1s linear;
  }
  
  .modal-close-button:hover,
  .modal-close-button:focus {
    color: #c6e8f2;
    font-size: 1.3em;
    outline: none;
  }
}


`



// // Projects.js Styles
export const StyledProjects = styled.div`
@import url('https://fonts.googleapis.com/css?family=KoHo:400,500&display=swap');
font-family: 'KoHo', sans-serif;
margin: 0 auto;
width: 1250px;
  

  @media(max-width: 600px){
    width: 100%;
    margin-top: 0px;
    
  }

  @media(max-width: 499px){
    padding-left: 25px;
  }
  
  .react-multi-carousel-list{
    height: 550px;
    @media(max-width: 500px){
      height: 430px;
    }
  }

  .projects-container{
    background: black !important;
    @media(max-width: 499px){
      margin-top: -80px;
    }
  }

  

  .custom-button-group{
    position: absolute;
    bottom: 0;
    left: 45%;
    display: flex;
    cursor: pointer;
    width: 350px;
    color: white;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;

    @media(max-width: 600px){
      left: 30%;
      position: fixed;
      bottom: 75px;
    }
  }
  .arrow{
    margin-right: 10px;
    font-size: 1.5rem;
    text-decoration: underline;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }

  .title-br{
  width: 360px;
  color: white;
  }

  .project-image{
    position: fixed;
    border: 15px solid #336075;
    width: 360px;
    height: 360px;
    border-top-right-radius: 7px;
    border-top-left-radius: 7px;
    top: 0;
    left: 0;
  }

  ul {
    list-style: none;
    margin-block-start: 0px;
    margin-block-end: 0px;
    padding-inline-start: 0px;
  }

  .card-ul{
    width: 205px;
    padding-top: 10px;
    text-align: left;
    margin: 0 auto;
    list-style: square;
  }

  .a-link {
    display: none;
  }

  .flippy-container {
    border-radius: 8px;
    height: 360px !important;
    overflow: hidden;
  }

  .flippy-back{
    border-radius: 8px;
    background: #336075;
    box-shadow: inset 41px 41px 0px #305b6f, 
            inset -41px -41px 0px #36657b;
  }

  .design-desc {
    text-align: center;
  }

  .framework-desc {
    text-align: center;
  }

  p {
    margin-block-start: 0px;
    margin-block-end: 0px;

    padding: 2%;
  }

  .card {
    position: relative;
   
   

    .click-here-text{
      position: absolute;
      bottom: 54px;
      font-size: 1.2em;
      cursor: default;

      &:hover{
        font-weight: 500;
      }

      @media(max-width: 600px){
        font-size: 1em;
      }
    }

    .click-here{
      position: absolute;
      bottom: 10px;
      right: 14px;
      text-transform: uppercase;
      font-size: 6em;
      color: #c6e8f2;

      @media(max-width: 600px){
        font-size: 3em;
        bottom: 40px;
      }

      &:hover{
        color: #A8C5FF;
        font-weight: 100;
      }
    }

    h3{
      font-weight: 700;
      margin-block-start: 0em;
      margin-block-end: 0em;
      color: white;
    }
  }

  .card-links {
    position: fixed;
    bottom: -31px;
    left: 31px;
    color: black;
    width: 360px;
    display: flex;
    justify-content: space-between;
    @media(max-width: 499px){
      left: 0px;
    }
    

    .link {
      text-decoration: none;
      padding: 2%;
      color: white;
      font-weight: 500;
      

      &:hover{
        color: #001920;
        font-weight: 500;
      }
    }

  }

  h3 {
    text-align: center;
    margin-block-start: .5em;
    margin-block-end: .5em;
  }

  h4 {
    text-align: center;
    font-weight: 500;
    margin-block-start: .1em;
    margin-block-end: .1em;
    
  }

  .proj-desc{
    width: 265px;
    margin: 0 auto;
    padding-top: 10px;
  }

  .react-multi-carousel-item--active{
    width: 390px !important;
    padding-left: 30px;

    @media(max-width: 499px){
      padding-left: 0px;
    }
  }

`;

//Footer.js Styles
export const StyledFooter = styled.div`
@import url('https://fonts.googleapis.com/css?family=KoHo:400,500&display=swap');
@media(min-width: 650px){
  display: none;
}
.footer-container{
  width: 100%;
  height: 70px;
  position: absolute;
  bottom: 10px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  z-index: 1;
  

  .footer-contents{
    display: flex;
    padding-left: 30px;
    color: white;
    align-items: center;
    font-family: 'KoHo', sans-serif;
    @media(max-width: 650px){
      padding-left: 0px;
      
    }
  }

  .footer-icon-container{
    width: 200px;
    display: flex;
    justify-content: space-between;
    @media(max-width: 650px){
      width: 100%;
      padding-left: 0px;
      justify-content: space-evenly;
    }
  }


  .png{
    height: 35px;
    width: 35px;
    @media(max-width:650px){
      height: 35px;
      width: 35px;
      background: #0b0b0b;
      border-radius: 8px;
    }
    &:hover{
      background: #0a0a0a;
     
    }
  }
}

`

